
import { defineComponent, onMounted, ref, computed } from "vue";
import { toPDFfromHTML } from "@/utils/Export";
import {
    IonTitle,
    IonPage,
    IonToolbar,
    IonContent,
    IonHeader,
    IonFooter,
    IonButton,
    IonImg,
    IonThumbnail,
    loadingController,
    modalController
} from "@ionic/vue"
import LegacyTemplate from "./LegacyTemplate.vue"
import { DrillPatients } from "@/utils/PatientDrilldown";
import { MultiStepPopupForm } from "@/utils/PopupKeyboard"
import { FieldType } from '@/components/Forms/BaseFormElements'
import { Option } from '@/components/Forms/FieldInterface'
import { isEmpty, isPlainObject } from "lodash";
import Validation from "@/components/Forms/validations/StandardValidations"
import HisDate from "@/utils/Date"
import { HtsReportService } from "@/apps/HTS/services/hts_report_service"
import { toastDanger } from "@/utils/Alerts";
import { useRoute } from 'vue-router';
import Img from "@/utils/Img"

const indicators = ref<any>()
const report = ref<string>('')
const year = ref('')
const quarter = ref('')
const facility = HtsReportService.getLocationName()||'Unknown'
const reportTypes: any = {
    "HTS LEGACY REGULAR": "HIV Testing (Regular Clients) – Combined Quarterly Site Report",
    "HTS LEGACY RETURNING": "HIV Testing (Regular returning Clients) – Combined Quarterly Site Report"
}

export default defineComponent({
    components: {
        IonPage,
        IonImg,
        IonThumbnail,
        IonTitle,
        IonToolbar,
        IonContent,
        IonHeader,
        IonFooter,
        IonButton,
        LegacyTemplate
    },
    setup() {
        const reportName = computed(() => reportTypes[report.value]||'Not set')

        const route = useRoute()

        const logo = Img('login-logos/Malawi-Coat_of_arms_of_arms.png')

        async function generateReport() {
            if (!year.value && !quarter.value) return
            const service = new HtsReportService(report.value||'');

            (await loadingController.create({ message: "Please wait"})).present()
            try {
                indicators.value = await service.requestReport({
                    year: year.value,
                    quarter: quarter.value
                })
            } catch (e) {
                toastDanger("Unable to generate report")
            }
            loadingController.getTop().then(v => v && loadingController.dismiss())
        }

        function configureReport() {
            MultiStepPopupForm([
                {
                    id: 'report',
                    helpText: "Report name",
                    type: FieldType.TT_SELECT,
                    isRequired: () => true,
                    computedValue: (v: Option) => v.value,
                    condition: () => !report.value,
                    options: () => {
                        return Object.keys(reportTypes).map((k) => {
                            return {
                                label: reportTypes[k],
                                value: k
                            }
                        })
                    }
                },
                {
                    id: 'year',
                    helpText: 'Select Year',
                    type: FieldType.TT_NUMBER,
                    isRequired: () => true,
                    computedValue: (v: Option) => v.value,
                    validation: (v: Option) => {
                        const year = isPlainObject(v) ? v.value : -1
                        return Validation.validateSeries([
                            () => {
                                if (isNaN(parseInt(`${year}`))) {
                                    return ['Invalid year']
                                }
                                return null
                            },
                            () => Validation.rangeOf(v, 2000, HisDate.getYear(HtsReportService.getSessionDate()))
                        ])
                    }
                },
                {
                    id: 'quarter',
                    helpText: 'Select Quarter',
                    type: FieldType.TT_SELECT,
                    isRequired: () => true,
                    computedValue: (v: Option) => v.value,
                    options: () => {
                        return [
                            {label: 'Q1', value: 'Q1'},
                            {label: 'Q2', value: 'Q2'},
                            {label: 'Q3', value: 'Q3'},
                            {label: 'Q4', value: 'Q4'}
                        ]
                    }
                }
            ],
            {
                skipSummary: true,
                formLabel: "Legacy report",
                onFinish: (f: any, c: any) => {
                    if (f.report) report.value = c.report
                    year.value = c.year
                    quarter.value = c.quarter
                    generateReport()
                    modalController.dismiss()
                }
            })
        }

        function downloadPDF() {
            toPDFfromHTML(`
                <html>
                    <body>
                        ${document.getElementById("report-content")?.innerHTML||'-'}
                    </body>
                </html>
            `, 50)
        }

        onMounted(() => {
            const prevReport = report.value
            if (reportTypes[`${route.query.report}`])
                report.value = `${route.query.report}`
            if (isEmpty(indicators.value) || prevReport != report.value) {
                year.value = ''
                quarter.value = ''
                indicators.value = null
                configureReport()
            }
        })
        return {
            reportName,
            facility,
            year,
            indicators,
            report,
            logo,
            quarter,
            DrillPatients,
            downloadPDF,
            configureReport,
            generateReport
        }
    }
})
